@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Mukta&display=swap');


#main-container{
    display: grid;
    place-items: center;
    position: relative;
    top: 20px;
}

.login-form{
    /* border: 1px solid black; */
    width: 450px;
    text-align: center;
    padding: 10px;
    box-shadow: 4px 7px 19px -3px rgba(136,130,130,0.75);
    border-radius: 10px;
    
}

.login-title{
    font-size: 40px;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 0;
    margin-top: 10px;
    cursor: context-menu;
}

.login-sub{
    /* width: 200px; */
    font-family: 'Mukta', sans-serif;
    cursor: context-menu;
}

.login-heading {
    display: grid;
    place-items: center;
}

.login-email, .login-password{
    padding: 10px;
    margin: 10px;
    width: 310px;
    border-radius: 5px;
    border: 1px solid #9F9E9E;
    font-family: 'Mukta', sans-serif;
    padding-left: 30px;
    font-size: 17px;
    /* outline:#F38978; */
}


.other-logins{
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid black; */

}

.other-login-logo{
    /* border: 1px solid black; */
    display: flex;
    padding: 10px;
    margin-left: 15px;
    font-family: 'Mukta', sans-serif;
    /* border: 1px solid #9F9E9E; */
    /* border-radius: 5px; */
}

.fa-brands{
    margin-top: 3px;
    margin-right: 10px;
}

.other-login-logo .fa-brands:hover{
    color: #F38978;
}

.login-links{
    margin: 0;
}

.login-button1{
    padding: 10px;
    width: 350px;
    border: none;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    background-color: #F38978;
    color: white;
    cursor: pointer;
}

.other-logins{
    display: flex;
}

.login-signup, .login-forgotPassword, .or-login-title{
    font-family: 'Mukta', sans-serif;
}

.login-forgotPassword, .or-login-title{
    color: #9F9E9E;
    cursor: context-menu;
}

.login-input .login-forgotPassword{
    text-align: left;
    padding-left: 53px;
    padding-top: 0;
    font-size: 15px;
}

.login-forgotPassword{
    cursor: pointer;
}
    

.login-password{
    margin-bottom: 0;
}


