@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap');

.emp-flex{
    display: inline-block;
}

.emp-container {
    width: 270px;
    height: 60vh;
    box-shadow: 9px 24px 24px 19px rgba(214, 211, 214, 1);
    border-radius: 10px;
    margin-top: 40px;
    margin-left: 50px;
}

.emp-nav {
    background: rgb(200, 198, 198);
    width: 15%;
    height: 7px;
    border-radius: 30px;
    position: relative;
    left: 44%;
    top: 4%;
}

#emp-page img {
    width: 200px;
    position: relative;
    top: -243px;
    left: 13%;
    transform: translate(-50% -50%);
}

#emp-page{
font-family: 'Oswald', sans-serif;
}
.emp-company {
    background: rgba(219, 55, 55, 0.31);
    width: 60px;
    height: 40vh;
    position: relative;
    top: 40px;
    left: 13%;
    transform: translate(-50% -50%);
    z-index: 20;
    cursor: context-menu;

}


.emp-text {
    position: relative;
    top: 230px;
    text-align: center;
    color: white;
}

#emp-name {
    position: relative;
    top: -210px;
    margin-left: 35px;
    display: inline-block;
}

.emp-name{
    margin-top: 30px;
    font-size: 30px;
    position: relative;
    top: -10px;
    cursor: context-menu;

}

.emp-id {
    color: gray;
    position: relative;
    top: -130px;
    left: 110px;
    margin: 0;
    cursor: context-menu;
}


#emp-name .fa-solid {
    background-color: gray;
    width: 20px;
    height: 10px;
    padding: 10px;
    text-align: center;
    position: relative;
    left: 180px;
    top: -75px;
    cursor: pointer;
    border-radius: 10px;
}
