@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mukta&display=swap');

body{
  background-color: #E5E5E5;
}

#home-page{
  display: flex;
  justify-content: space-around;
  font-family: 'Lato', sans-serif;
}
li{
  list-style-type: none;
  display: inline-block;
  padding: 20px;
  letter-spacing: 1.5;
}

#home-container{
  margin-left: 10%;
}

.home-button{
  display: flex;
  width: 600px;
}
.home-image{
  width: 600px;
  height: 80vh;
  position: relative;
  left: 70px;
  top: 20px;
}

.home-middle-content{
  display: flex;
}

.home-me{
  width: 500px;
  height: 50vh;
  margin-top: 70px;
}

.home-myName{
  font-size: 60px;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 10px;
  cursor:context-menu;
}

span{
  color: #F38978;
}

.home-about{
  font-size: 15px;
  font-family: 'Mukta', sans-serif;
  cursor:context-menu;
}

.home-role{
  font-family: 'Poppins', sans-serif;
  margin-top: 0;
  cursor:context-menu;
}

.home-about-me{
  width: 100px;
  text-align: center;
  padding: 15px;
  border-radius: 10px;
  background-color: #F38978;
  font-family: 'Poppins', sans-serif;
  position: relative;
  top: 30px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  list-style: none;
}

a {
  text-decoration: none;
  color: white;
}

.home-about-me:hover{
  transform: scale(1.15);
}

.home-icons {
  height: 100px;
}

.home-icons .fa-brands{
  padding: 13px;
  border-radius: 30px;
  margin-left: 20px;
  font-size: 20px;
  color: #6C6C6C;
  position: relative;
  top: -80px;
  cursor: pointer;
  border: 1px solid #E5E5E5;

}

.home-icons .fa-brands:hover{
  color: #F38978;
  border-radius: 30px;
  box-shadow: inset 3px 3px 7px #ffffffb0,
    inset -3px -3px 7px rgba(94, 104, 121, 0.692);
}

