@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');

ul{
    margin-top: 20px;
    margin-bottom: 0;
}

nav li{
    display: inline-block;

}
nav a{
    color: black;
    text-decoration: none;
    padding: 20px;
    display: block;
    font-family: 'Lato', sans-serif;
    transition: 0.3s ease-in-out;
    padding: 0 10px;

}
nav a:hover{
  transform: scale(1.15);
}

nav a:after{
    content: "";
    position: absolute;
    background-color: #F38978;
    height: 2px;
    width: 0;
    left: 0;
    bottom: -10px;
    transition: 0.3s;
}

nav a:hover:after{
    width: 100%;
}

a.active{
    color: #F38978;
    
}

