body{
    background-color: #E5E5E5;
}

#container{
    
    width: 350px;
    height: 80vh;
    position: absolute;
    top: 15%;
    left: 38%;
    transform: translate(-50% -50%);
    border-radius: 10px;
    box-shadow: 7px 8px 20px 1px rgba(139,139,139,0.75);
}

#display{
    background-image: linear-gradient(to bottom, #fcfcff, #e0e0e5, #c5c6cc, #aaabb3, #90929b);
    width: 100%;
    height: 25vh;
    border-radius: 10px;
    padding-top: 60px;
    /* box-sizing: content-box; */
    /* display: inline-block; */
    
}

#display h1 {
    /* width: 100%; */
    word-wrap: break-word;
}
#main{
    display: flex;
    justify-content: space-around;
    padding: 0;
    position: relative;
    top: 100px;
    left: 2%;

}

.numbers {
padding: 10px;
margin: 10px;
text-align: center;
font-family: 'Oswald', sans-serif;
border: none;
border-radius: 40px;
font-family: 'Gabarito', sans-serif;
/* color: #03394D; */
cursor: pointer;
position: relative;
top: -90px;
transition: 0.3s ease-in-out;

}

.numbers:hover{
    transform: scale(1.15);
    box-shadow: 9px 9px 20px -2px rgba(162,160,160,0.75) inset;
    border-radius: 25px;
}

.div-1{
    font-weight: bolder;
}

.div-2{
    color: #6E6E6E; 
}

.div-3{
    color: #F38978;
    font-weight: bold;
}

.div-4{
    background-color: #F38978;
    margin: 0;
    color: white;
    font-weight: bold;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    border-radius: 20px;
    box-shadow: 9px 7px 20px 1px rgba(243,137,120,0.75);
    margin-left: 0;
}

