@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mukta&display=swap');


.Details{
    display: flex;
}

#about-me{
    display: flex;
}

.about-me-details{
    margin-left: 10%;
    margin-top: 2%;
}

.my-img{
    width: 450px;
    background-color: #F38978;
    clip-path: polygon(72% 28%, 82% 68%, 42% 86%, 26% 65%, 18% 37%, 32% 15%, 57% 7%, 93% 16%, 84% 46%, 83% 82%, 56% 95%, 17% 87%, 10% 52%, 10% 36%, 13% 25%, 19% 15%);
    position: relative;
}

.my-cv{
width: 150px;
text-align: center;
padding: 15px;
border-radius: 10px;
background-color: #F38978;
font-family: 'Poppins', sans-serif;
color: white;
position: relative;
top: 30px;
margin-top: 0;
cursor: pointer;
transition: 0.3s ease-in-out;
}

.my-cv:hover{
transform: scale(1.15);
}


.about-me-name,.about-me-skills{
    font-size: 40px;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 0;
}

.about-me-name{
    margin-top: 0;
}

.about-me-sub{
    font-family: 'Mukta', sans-serif;
    width: 900px;
    margin-bottom: 0;
}

.me-color{
    color: #F38978;
}

.about-me-moreDetails, .about-me-colon, .about-me-answers{
    font-family: 'Mukta', sans-serif;
    margin-right: 40px;
}

.about-me-colon, .about-me-moreDetails{
    font-weight: bold;
}


.about-me-skills{
    display: flex;
}

.skills{
    color: #F38978;
}

.skills-are{
    font-size: 40px; 
    text-align: left;
    font-family: 'Poppins', sans-serif;
    margin: 0;
}

