table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

table td,
table th {
    border: 1px solid #ddd;
    padding: 8px;
}

table tr:nth-child(even) {
    background-color: #f2f2f2;
}

table tr:hover {
    background-color: #ddd;
}

table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
    text-transform: uppercase;
}

.buttons{
    display: flex;
    justify-content: center;
    margin-top: 50px;
    ;
}

.pg-btn{
    padding: 15px 18px 15px 18px;
    /* border-radius: 25px; */
    border-radius: 50px;
    margin: 10px;
    border: 1px solid #9A9A9A;
    cursor: pointer;
    /* background-color: red */
}

.pg-btn1{
    border: none;
    background-color: none;
    cursor: pointer;

}

.pg-btn.one{
    background-color: #04AA6D;
    
}

.pg-btn1:disabled{
    color: #b3b3b3;
    pointer-events: none;
}

button.active {
    background-color: #04AA6D;
    color: white;

}

