@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Mukta&display=swap');


#signup-main-container{
    display: grid;
    place-items: center;
    position: relative;
    top: 20px;
    
}

.signup-form{
    /* border: 1px solid black; */
    width: 450px;
    text-align: center;
    padding: 10px;
    box-shadow: 4px 7px 19px -3px rgba(136,130,130,0.75);
    border-radius: 10px;
    padding: 30px;
    height: 70vh;  
}

.signup-privacy-policy{
    display: flex;
    font-family: 'Mukta', sans-serif;
    margin-left: 38px;
    cursor: context-menu;
}

.signup-color{
    color: #F38978;
}

.signup-title{
    font-size: 40px;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 0;
    margin-top: 10px;
    cursor: context-menu;
}


.signup-heading {
    display: grid;
    place-items: center;
}

.signup-first-name, .signup-last-name, .signup-email, .signup-password, .signup-confirm-password{
    font-family: 'Mukta', sans-serif;
    border: 1px solid #9F9E9E;
    width: 350px;
    padding: 10px;
    margin: 15px;
    border-radius: 5px;
}

.signup-first-name, .signup-last-name{
    width: 148px;
}

.signup-button1{
    padding: 10px;
    width: 350px;
    border: none;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    background-color: #F38978;
    color: white;
    cursor: pointer;
}

.login-input .login-forgotPassword{
    text-align: left;
    padding-left: 53px;
    padding-top: 0;
    font-size: 15px;
}
